export function decodeKey(str) {
    let config = {
        alt: false,
        ctrl: false,
        code: null
    }

    str.split('+').forEach(param => {
        if (param === 'CTRL') {
            config.ctrl = true;
        } else if (param === 'ALT') {
            config.alt = true;
        } else {
            config.code = param;
        }
    })

    return config
}

export function getEventKeyCode(ev) {
    let code;
    if (ev instanceof PointerEvent || ev instanceof MouseEvent) {
        switch (ev.button) {
            case 0:
                code = 'LMB';
                break;
            case 1:
                code = 'MMB';
                break;
            case 2:
                code = 'RMB';
                break;
            case 3:
                code = '4MB';
                break;
            case 4:
                code = '5MB';
                break;
        }
    } else {
        code = ev.code;
    }
    return code;
}
export function stringifyKeyEvent(ev) {
    let code = getEventKeyCode(ev);

    let out = '';
    if (ev.altKey) {
        out += 'ALT+'
    }
    if (ev.ctrlKey) {
        out += 'CTRL+'
    }
    return out + code
}

export function calculateColumnSize() {
    const columns = $('.column', topMenuContent);
    const windowWidth = window.innerWidth;

    const colWidth = windowWidth / columns.length;

    $('.column', topMenuContent).css('width', colWidth);
}

export function htmlspecialchars(text) {
    if(!text) return '';
    
    return text
        .replace(/&/g, "&amp;")
        .replace(/</g, "&lt;")
        .replace(/>/g, "&gt;")
        .replace(/"/g, "&quot;")
        .replace(/'/g, "&#039;");
}

export function getRecommendedColorSize() {
    if (globals.mobile) return 24;
    const max = 30;
    const p = $('#palette');
    // 14 is for palette padding 
    let size = Math.floor((window.innerWidth - 14) / p.children().length);
    size = Math.min(size, max);

    return size
}

export function getPathsafeDate() {
    const date = new Date;

    const day = date.getDate().toString().padStart(2, '0'); // convert "1"s to "01"s
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const year = date.getFullYear();

    const today = `${day}.${month}.${year}`;

    const hours = date.getHours().toString().padStart(2, '0');
    const minutes = date.getMinutes().toString().padStart(2, '0');
    const seconds = date.getSeconds().toString().padStart(2, '0');

    const time = `${hours}-${minutes}-${seconds}`;

    return `${today} - ${time}`
}

export function testPointInPolygon(nvert, vertx, verty, testx, testy) {
    // copied and translated to js from some stackoverflow
    let i, j, c = 0;
    for (i = 0, j = nvert - 1; i < nvert; j = i++) {
        if (((verty[i] > testy) != (verty[j] > testy)) &&
            (testx < (vertx[j] - vertx[i]) * (testy - verty[i]) / (verty[j] - verty[i]) + vertx[i]))
            c = !c;
    }
    return c;
}

export function resizeCanvas(canvas, newWidth, newHeight) {
    const tempCanvas = document.createElement('canvas');
    const tempCtx = tempCanvas.getContext('2d');

    tempCanvas.width = newWidth;
    tempCanvas.height = newHeight;

    tempCtx.imageSmoothingEnabled = false;
    tempCtx.drawImage(canvas, 0, 0, newWidth, newHeight);

    const resizedCanvas = document.createElement('canvas');

    resizedCanvas.width = newWidth;
    resizedCanvas.height = newHeight;

    const resizedCtx = resizedCanvas.getContext('2d');

    resizedCtx.imageSmoothingEnabled = false;
    resizedCtx.drawImage(tempCanvas, 0, 0, newWidth, newHeight);

    return resizedCanvas;
}

export function loadImage(url) {
    return new Promise((res, rej) => {
        const img = new Image();
        img.src = url;

        img.onerror = rej;
        img.onload = () => res(img);
    })
}

export function reverseFade(el) {
    let reboundInt = setInterval(() => {
        redrawFade();
    }, 100);

    const fadeCanvas = document.createElement('canvas');
    const ctx = fadeCanvas.getContext('2d');

    fadeCanvas.style.cssText =
        `position: absolute;
        z-index: 999`;

    window.addEventListener('resize', onresize)
    function onresize() {
        fadeCanvas.width = window.innerWidth;
        fadeCanvas.height = window.innerHeight;

        redrawFade();
    }
    onresize();

    function redrawFade() {
        ctx.clearRect(0, 0, fadeCanvas.width, fadeCanvas.height);

        ctx.fillStyle = 'black';
        ctx.globalAlpha = 0.8;
        ctx.fillRect(0, 0, fadeCanvas.width, fadeCanvas.height);

        const bnds = el.getBoundingClientRect()

        ctx.clearRect(bnds.x, bnds.y, bnds.width, bnds.height);
    }

    function clear() {
        clearInterval(reboundInt);
        window.removeEventListener('resize', onresize);
        fadeCanvas.remove();
    }

    document.body.appendChild(fadeCanvas);

    return clear;
}

export function makeScreenshot() {
    const canvas = globals.chunkManager.dumpAll();

    const link = document.createElement('a');
    link.download = `GX ${canvasName} ${getPathsafeDate()}.png`;
    link.href = canvas.toDataURL()
    link.click();
}

export async function sleep(ms) {
    return new Promise(res => setTimeout(res, ms));
}

// calculates mouse down/up coords and tells is it drag or click
export function isClick(downPos, upPos, threshold=5){
    return Math.abs(downPos[0] - upPos[0]) < threshold &&
        Math.abs(downPos[1] - upPos[1]) < threshold;
}